body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-size: 62.5%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #232b2b;
    color: 	#3b444b;
}
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.dashboard {
    margin-top: 1rem;
}
.nav-wrapper {
    background-color: #353839;
}
nav .right li:first-child{
    margin-right: 1rem;
}
form {
    padding: 20px;
    margin-top: 60px;
    border-radius: 10px;
}
form button, form h5 {
    margin: 20px 0;
}
.btnSignUP {
    width: 80%;
    height: 60px;
    font-size: 1.8rem;
}
.iconsBlock {
    position: absolute;
    top: .5rem;
    right: .2rem;
}
.btnBack {
    position: absolute;
    top: 1rem;
    right: 1rem;
}
.iconsBlock i {
    cursor: pointer;
}
.modal-container {
    position: fixed;
    top: 0;
    left: 0;
    min-height: 100vh;
    width: 100%;
    background-color: rgba(0, 0, 0, .7);
    z-index: 2;
}
.modal {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 30%;
    left: 50%;
    transform: translate(-50%);
    z-index: 2;
}
.main-loader {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    width: 100%;
}
.circle-clipper .circle {
    border-width: 10px;
}
.projects-header {
    font-size: 1.5rem;
    color: ivory;
    font-weight: bold;
    margin-top: 1rem;
}
.projects-header-item {
    border-right: 4px solid ivory;
    border-top: 4px solid ivory;
    height: 100%;
    padding-top: 2rem !important;
    position: relative;
    line-height: 1.5rem;
}
.projects-header-item::after {
    content: "";
    position: absolute;
    height: 70%;
    width: 100%;
    top: 100%;
    left: 4px;
    border-right: 4px solid ivory;
}
.projects-header-item:last-child, .projects-header-item:last-child::after {
    border-right: none;
}
.projects-content {
    margin-top: 2rem;
}
.btn-group {
    display: flex;
    width: 100%;
    justify-content: space-between;
}
.card .card-content {
    padding: 2rem 2rem 0 2rem;
}
.checkbox {
    align-self: center;
}
.checkbox label {
    color: ivory;
}
[type="checkbox"]+span:not(.lever):before, [type="checkbox"]:not(.filled-in)+span:not(.lever):after {
    border: 2px solid ivory;
}


@media only screen and (max-width: 992px) {
    nav .brand-logo {
        left: 25%;
        transform: translateX(-50%);
    }
}

@media only screen and (max-width: 600px) {
    nav .right {
        width: 100%;
        margin: 0 auto;
        display: flex;
        justify-content: space-around;
    }

    nav .right .navLink {
        font-size: 1.5rem;
    }

    .projects-header {
        margin-top: 3rem;
    }
}

@media only screen and (min-width: 993px) {
    .container {
        width: 90%;
    }
}
